.near-you-layout {
    display: flex;
    gap: 40px;
    align-items: flex-start;
    min-height: 400px;
    margin-bottom: 50px;
    margin-top: 20px;
    padding: 0 30px;
}

.address-section {
    flex: 1;
}

.address-box {
    background-color: var(--white);
    border: 2px solid var(--primary-blue);
    padding: 40px;
    border-radius: 12px;
    min-height: 250px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    box-shadow: 0 4px 15px rgba(0,0,0,0.1);
}

.address-box h3 {
    color: var(--primary-blue);
    font-size: 2em;
    text-align: center;
    margin-bottom: 30px;
    width: 100%;
    font-weight: bold;
}

.map-section {
    flex: 2;
}

.map-container {
    background-color: var(--white);
    padding: 40px;
    border-radius: 12px;
    border: 2px solid var(--primary-blue);
    box-shadow: 0 4px 15px rgba(0,0,0,0.1);
}

.map-container h3 {
    color: var(--primary-blue);
    font-size: 2em;
    margin-bottom: 25px;
    font-weight: bold;
    text-align: center;
}

.map-wrapper {
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 75%;
    overflow: hidden;
    border-radius: 8px;
}

.map-wrapper iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 8px;
}

.additional-info {
    background-color: var(--light-gray);
    padding: 40px;
    border-radius: 12px;
    margin: 40px 30px;
    box-shadow: 0 4px 15px rgba(0,0,0,0.1);
}

.additional-info h3 {
    color: var(--primary-blue);
    font-size: 2em;
    margin-bottom: 25px;
    text-align: center;
    font-weight: bold;
}

.additional-info > p {
    color: var(--dark-gray);
    font-size: 1.2em;
    margin-bottom: 20px;
    text-align: center;
    line-height: 1.6;
}

.additional-info ul {
    list-style-type: none;
    padding: 0;
}

.clickable-item {
    cursor: pointer;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.clickable-item:hover {
    transform: translateY(-3px);
    box-shadow: 0 6px 20px rgba(0, 0, 0, 0.15);
}

.court-item-wrapper {
    position: relative;
    margin-bottom: 30px;
}

.court-actions {
    display: flex;
    gap: 10px;
    margin-top: 10px;
    justify-content: flex-end;
}

.edit-btn,
.delete-btn {
    padding: 8px 20px;
    font-size: 0.9em;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease;
    font-weight: bold;
}

.edit-btn {
    background-color: #28a745;
    color: var(--white);
}

.edit-btn:hover {
    background-color: #218838;
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(40, 167, 69, 0.3);
}

.delete-btn {
    background-color: #dc3545;
    color: var(--white);
}

.delete-btn:hover {
    background-color: #c82333;
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(220, 53, 69, 0.3);
}

.loading-message,
.error-message {
    text-align: center;
    padding: 40px 20px;
    font-size: 1.2em;
    color: #666;
}

.error-message {
    color: #d32f2f;
}

.add-court-section {
    background-color: var(--white);
    border: 2px solid var(--primary-blue);
    border-radius: 12px;
    padding: 40px;
    margin: 40px 30px;
    box-shadow: 0 4px 15px rgba(0,0,0,0.1);
}

.add-court-header {
    text-align: center;
    margin-bottom: 30px;
}

.add-court-header h3 {
    color: var(--primary-blue);
    font-size: 2em;
    margin-bottom: 15px;
    font-weight: bold;
}

.add-court-header p {
    color: var(--dark-gray);
    font-size: 1.1em;
    margin-bottom: 20px;
    line-height: 1.6;
}

.toggle-form-btn {
    background-color: var(--primary-blue);
    color: var(--white);
    border: none;
    padding: 12px 30px;
    font-size: 1.1em;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease;
    font-weight: bold;
}

.toggle-form-btn:hover {
    background-color: #3a8eef;
    transform: translateY(-2px);
    box-shadow: 0 4px 12px rgba(74, 158, 255, 0.3);
}

.court-form {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.form-row {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
}

.form-group {
    display: flex;
    flex-direction: column;
}

.form-group.full-width {
    grid-column: 1 / -1;
}

.form-group label {
    color: var(--primary-blue);
    font-weight: bold;
    margin-bottom: 8px;
    font-size: 1em;
}

.form-group input {
    padding: 12px;
    border: 2px solid var(--primary-blue);
    border-radius: 8px;
    font-size: 1em;
    transition: border-color 0.3s ease, box-shadow 0.3s ease;
    font-family: Arial, sans-serif;
}

.form-group input:focus {
    outline: none;
    border-color: #3a8eef;
    box-shadow: 0 0 0 3px rgba(74, 158, 255, 0.1);
}

.form-group input.error {
    border-color: #d32f2f;
}

.form-group input.error:focus {
    border-color: #d32f2f;
    box-shadow: 0 0 0 3px rgba(211, 47, 47, 0.1);
}

.error-message {
    color: #d32f2f;
    font-size: 0.9em;
    margin-top: 5px;
    font-weight: normal;
}

.submit-message {
    padding: 15px;
    border-radius: 8px;
    font-size: 1em;
    text-align: center;
    font-weight: bold;
    margin-top: 10px;
}

.submit-message.success {
    background-color: #d4edda;
    color: #155724;
    border: 2px solid #c3e6cb;
}

.submit-message.error {
    background-color: #f8d7da;
    color: #721c24;
    border: 2px solid #f5c6cb;
}

.submit-btn {
    background-color: var(--primary-blue);
    color: var(--white);
    border: none;
    padding: 14px 40px;
    font-size: 1.2em;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease;
    font-weight: bold;
    margin-top: 10px;
    align-self: center;
    min-width: 200px;
}

.submit-btn:hover {
    background-color: #3a8eef;
    transform: translateY(-2px);
    box-shadow: 0 4px 12px rgba(74, 158, 255, 0.3);
}

.submit-btn:active {
    transform: translateY(0);
}

@media (max-width: 768px) {
    .near-you-layout {
        flex-direction: column;
        gap: 20px;
        padding: 0 20px;
    }
    
    .address-box {
        min-height: 150px;
        padding: 20px;
    }
    
    .map-container {
        padding: 20px;
    }

    .map-container h3 {
        font-size: 1.6em;
        margin-bottom: 15px;
    }

    .map-wrapper {
        padding-bottom: 100%;
    }
    
    .additional-info {
        padding: 20px;
        margin: 20px;
    }

    .add-court-section {
        padding: 20px;
        margin: 20px;
    }

    .form-row {
        grid-template-columns: 1fr;
        gap: 15px;
    }

    .add-court-header h3 {
        font-size: 1.6em;
    }

    .add-court-header p {
        font-size: 1em;
    }

    .court-actions {
        flex-direction: column;
        gap: 8px;
    }

    .edit-btn,
    .delete-btn {
        width: 100%;
    }
}

